.item {
    position: relative;
    width: 480px;
    height: 270px;
    margin: 50px;
    box-shadow: 0 0 5px 5px #112d32;
    border-radius: 25px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
}
.item h3 {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    min-height: 50px;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.6274509804);
    border-radius: 0 0 25px 25px;
    text-align: center;
    font-size: 1.5em;
    color: rgba(255, 255, 255, 0.8);
}

.item:hover {
    animation: scaling 0.5s ease 0s 1;
    box-shadow: 0 0 25px 5px #112d32;
    transform: scale(1.1);
}

@keyframes appear {
    0% {
        box-shadow: none;
        opacity: 0;
    }
    100% {
        box-shadow: 0 0 5px 0 #112d32;
        opacity: 0.8;
    }
}
.buttons {
    width: 80%;
    margin-top: -50px;
    margin: 0 auto;
}
.buttons a {
    width: calc(50% - 10px);
    height: 50px;
    margin: 5px;
    display: inline-block;
    text-align: center;
    transform: translate(0, -45px);
    border-radius: 15px;
    background-color: #aaabb8;
    line-height: 50px;
    opacity: 0.8;
    color: #112d32;
    font-size: 1.2em;
    font-weight: 400;
    box-shadow: 0 0 5px 0 #112d32;
    animation: appear 0.5s ease 0s 1;
}
.buttons a:hover {
    opacity: 1;
}

