.startScreen {
    width: 100%;
    height: calc(100vh - 80px);

    padding: 10px;

    background-color: #112d32;
    color: #88bdbc;
    text-align: center;
}

.heading {
    margin-top: 250px;

    font-size: 5em;
}

.subHeading {
    margin-top: 20px;
    font-size: 2em;
}

.socials {
    margin-top: 100px;
}

.icon {
    margin: 0 20px;

    font-size: 3em;
}
a:hover .icon {
    color: #88bdbc;
}

a:active .icon {
    color: #254e58;
}
